<!--  -->
<template>
    <div class="detail">
        <top-back :title='name'></top-back>
        <div class="detail-text">
          <div class="detail-title">
             <p>{{detail.title}}</p>
             <div class="detail-time">
                <div class="de-size" >
                    {{detail.opendate}}
                </div>
                <div class="de-size" >
                    {{clickCount}}
                </div>
             </div>
             <div class="detail-time" >
                   <div class="det-size" >
                        {{detail.projDq}}
                    </div>
                    <div class="det-size">
                        {{detail.pbMode}}
                    </div>      
             </div>
             <div class="m-15">
                    {{detail.zzRank ? detail.zzRank : ''}}
             </div>
          </div>
          <div class="detail-cli" @click="topush(detail)" :class="this.total == null ? 'current' : ''" >
             <div  >
                
             </div>
             <div>
                <van-icon name="arrow" />
             </div>
          </div>
          <div class="detail-cli" @click="jump" >
             <div>
                
             </div>
            <div >
                 <van-icon name="arrow" />
             </div>
          </div>
          <div class="detail-contant" v-html="detail.content"  >
             
          </div>
        </div>
    </div>
</template>
<script>
import topBack from '@/components/topback'
export default {
    name: 'templateName', // 
    data() {
        return {
            // 
            id:'',
            source:'',
            detail:{},
            clickCount:0,
            name:'',
            total:0
        }
    },
    watch: {
        // 
    },
    components: {
      'top-back':topBack  
    },
    props: {
        // 
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        this.id = this.$route.query.id
        this.source = this.$route.query.source
        let that=this;
        this.$http({
            method:'post',
            url: '/notice/detail/' + that.id,
            data:{
                source:that.source,
                type: "0"
            }
        }).then(function(res){
           that.total = res.data.relCompanySize 
           
           that.detail = res.data.data[0]  
           that.detail.projDq = that.detail.projDq.substring(0,2)
           that.clickCount = res.data.clickCount       
        })
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
         topush(o) {
             if(this.total == null) {
                 return 
             }
            this.$router.push({path:'/conform',query:{id:o.id,source:o.source}})
        },
        jump() {
            window.open(this.detail.url)
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style   lang="less" scoped >
.detail {
 position: absolute;
 top: 0;
 left: 0;
 right: 0;
 bottom: 0;

 .detail-text {
   margin-top: 91px;
   height: calc(~"100% - 101px");
   overflow-x: hidden;
   overflow-y: auto;
   font-size: 32px;
   color:#333;
   .detail-title {
     padding: 35px;
     p {
       text-align: center;
     }
     .detail-time {      
        display: flex;
        justify-content: space-between;
        margin-top: 23px;
        .de-size {
          font-size: 20px;
          color:#999;
        }
        .det-size{
          font-size: 24px;
          color:#999;
        }
     }
      .m-15 {
        margin-top: 15px;
        font-size: 24px;
        color:#999;
      }
     
   }
   .detail-cli {
      height: 88px;
      line-height: 88px;
      font-size: 32px;
      color:#333;
      padding: 0 35px; 
      display: flex;
      justify-content: space-between;
      border-bottom: 1PX solid #F2F2F2;
   }
   .detail-contant {
     padding: 35px;
   }
 }
}
#app .detail .current {
    opacity: 0.3;
}
</style>



