        <div class="top">
            <p class="icon">
                <img src="../assets/icon-zhao.png"/>
            </p>
            <p class="tit">{{obj.title}}</p>
        </div>
        <div class="center">{{obj.certificate ? obj.certificate : ''}}</div>
        <div class="bottom">
            <p>{{obj.pbMode ? obj.pbMode : ''}}</p>
            <p>{{obj.opendate ? obj.opendate : ''}}</p>
        </div>
    </div>
</template>
<script>
// let moment = require("moment");
export default {
