<!--  DOM  -->
<template>
    <div class="getType">
        <ul>
            <li v-for="(o,i) of addList" :key="i">
                <span :class="tabNum==i?'active':''" @click="tapFn(i)">{{o.name}}</span>
            </li>
        </ul>
        <div class="btn">
            <button class="canle" @click="canleFn"></button>
            <button class="sure" @click="sureFn"></button>
        </div>
        
    </div>
</template>
<script>
export default {
    name: 'getType', // 
    data() {
        return {
            // 
            addList:[
                {
                    return:'',
                    name:'',
                },{
                    return:'0',
                    name:'',
                },{
                    return:1,
                    name:'',
                },{
                    return:2,
                    name:'',
                },{
                    return:3,
                    name:'',
                },{
                    return:4,
                    name:'',
                },{
                    return:5,
                    name:'',
                }
            ],
            tabNum:0,
        }
    },
    watch: {
        // 
    },
    props: {
        // 
        num:{
            default:0
        }
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        this.tabNum=this.num
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        tapFn(i){
            this.tabNum=i;
        },
        sureFn(){
            let str=this.addList[this.tabNum].return;
            this.$parent.mask=false;
            this.$emit('sureFn',{str:str,index:this.tabNum});
        },
        canleFn(){
            this.$parent.mask=false;
            this.$emit('canleFn');
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
.getType{
    position: absolute;
    background: #f5f5f5;
    bottom:0;
    transform: translateY(100%);
    left: 0;
    z-index: 9;
    width: 100%;
    padding:50px 0 0;
    box-sizing: border-box;
    ul{
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 150px;
        li{
            width:33%;
            text-align: center;
            margin-bottom: 32px;
            span{
                display: inline-block;
                border-radius:10px;
                text-align: center;
                width: 176px;
                height: 56px;
                line-height: 56px;
                color: #999;
                border: 1PX solid #CCBEBE;
                background: #fff;
            }
            .active{
                border-color: #FE6603;
                background: #FE6603;
                color: #fff;
            }
        }
    }
    .btn{
        display: flex;
        button{
            height: 84px;
            flex-grow:1;
        }
        .sure{
            background: #FE6603;
            border: 1PX solid #FE6603;
            color: #fff;
        }
        .canle{
            border: 1PX solid #CCBEBE;
            color: #333;
            background: #fff;
        }
    }
    
}
</style>



