<!--  DOM  -->
<template>
    <div class="addr">
        <ul>
            <li v-for="(o,i) of addList" :key="i">
                <span :class="tabNum==i?'active':''" @click="tapFn(i)">{{o}}</span>
            </li>
        </ul>
        <button @click="sureFn"></button>
    </div>
</template>
<script>
export default {
    name: 'addr', // 
    data() {
        return {
            // 
            addList:["","","","","","","","","","","","","","","","","","","","","","","","","","","","","","",""],
            tabNum:'',
        }
    },
    watch: {
        // 
    },
    props: {
        // 
        add:{
            default:''
        }
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        let addr=this.add;
        let arr=this.addList;
        let that=this;
        for(let x=0;x<arr.length;x++){
            if(arr[x]==addr){
                that.tabNum=x
            }
        }
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        tapFn(i){
            this.tabNum=i;
        },
        sureFn(){
            let str=this.addList[this.tabNum];
            this.$parent.mask=false;
            this.$emit('addObj',str);
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
.addr{
    position: absolute;
    transform: translateY(100%);
    background: #f5f5f5;
    bottom:0;
    left: 0;
    z-index: 9;
    width: 100%;
    padding:50px 0 0;
    box-sizing: border-box;
    ul{
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 70px;
        li{
            width:20%;
            text-align: center;
            margin-bottom: 25px;
            span{
                padding:5px 10px;
                display: inline-block;
                border-radius: 8px;
                text-align: center;
            }
            .active{
                background: #FE6603;
                color: #fff;
            }
        }
    }
    button{
        width: 100%;
        height: 84px;
        background: #FE6603;
        color: #fff;
        border: none;
    }
}
</style>



