<!--  DOM  -->
<template>
    <div class="listCon">
        <!--  -->
        <template v-if="type=='fw'">
            <h5>{{obj.title}}</h5>
            <p>{{obj.court}}</p>
            <p>{{obj.caseNo}}</p>
            <p>{{obj.dateStr}}</p>
        </template>
        <!--  -->
        <template v-else-if="type=='zz'">
            <h5>{{obj.qualName}}</h5>
            <p>{{obj.certOrg}}</p>
            <p>{{obj.certNo}}</p>
            <p>{{obj.certDate}}</p>
        </template>
        <!--  -->
        <template v-else-if="type=='ry'">
            <h5>{{obj.name}}</h5>
            <p>{{obj.category}}</p>
            <p>  {{obj.major}}</p>
            <p>{{obj.validDate}}</p>
        </template>
        <!--  -->
        <template v-else-if="type=='yj'">
            <h5>{{obj.proName}}</h5>
            <p>{{obj.proOrg}}</p>
            <p>{{obj.province}}</p>
            <p>{{obj.proType}}</p>
        </template>
        <!--  -->
        <template v-else-if="type=='zb'">
            <h5>{{obj.title}}</h5>
            <p>{{obj.oneName}}</p>
            <p>{{obj.oneOffer}}</p>
            <p>{{obj.opendate}}</p>
        </template>
        <!--  -->
        <template v-else-if="type=='gb'">
            <h5>{{obj.mateName}}</h5>
            <p>{{obj.projName}}</p>
            <p>{{obj.years}}</p>
        </template>
        <!--  -->
          <template v-else-if="type=='bl'">
            <h5>{{obj.nature}}</h5>
            <p>{{obj.projectName}}</p>
            <p>{{obj.publishSite}}</p>
            <p>{{obj.publishDate}}</p>
            <p>{{obj.badBehaviorContent}}</p>
        </template>
    </div>
</template>
<script>
// let moment = require("moment");
export default {
    name: 'listCon', // 
    data() {
        return {
            // 
        }
    },
    watch: {
        // 
    },
    props: {
        // 
        obj:{},
        type:{
            default:'fw'
        }
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {

        // console.group('===============created');
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        // 
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
.listCon{
    background: #fff;
    padding: 38px 20px;
    margin-bottom: 18px;
    border-radius: 8px;
    h5{
        font-size: 28px;
        font-weight: normal;
        margin-bottom: 26px;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space:nowrap
    }
    p{
        font-size: 24px;
        color: #999;
        margin-bottom: 26px;
        overflow: hidden;
    }
    p:last-child{
        margin-bottom: 0
    }
}
</style>



