<!--  DOM  -->
<template>
    <div class="winning">
       <top-back :title='name'></top-back>
        <div class="win-text">
          <div class="win-title">
            <p>
              {{detail.title}}
            </p>
            <div class="win-time">
              <div class="win-c" >
                {{detail.opendate}}
              </div>
              <div class="win-c">
                {{clickCount}}
              </div>
            </div>
             <div class="win-one">
                {{detail.oneName}}
              </div>
              <div class="win-one" >
                {{detail.oneOffer ? detail.oneOffer + '' : ''}} 
              </div>
          </div>
          <div class="win-to" @click="jump" >
            <div>
              
            </div>
            <div >
                 <van-icon name="arrow" />
             </div>
          </div>
          <div class="win-contant" v-html="detail.content"  >

          </div>
        </div>
    </div>
</template>
<script>
import topBack from '@/components/topback'
export default {
    name: 'winning', // 
    data() {
        return {
            // 
            name:'',
            detail:{},
            clickCount:0,
            id:'',
            source:'',
        }
    },
    watch: {
        // 
    },
    components: {
      'top-back':topBack  
    },
    props: {
        // 
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        this.id = this.$route.query.id
        this.source = this.$route.query.source
        let that=this;
        this.$http({
            method:'post',
            url: '/notice/detail/' + that.id,
            data:{
                source:that.source,
                type: "2"
            }
        }).then(function(res){
           that.detail = res.data.data[0]
           console.log(res.data)
           that.clickCount = res.data.clickCount
        })
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        jump() {
             window.open(this.detail.url)
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less" > 
.winning {
 position: absolute;
 top: 0;
 left: 0;
 right: 0;
 bottom: 0;
 .win-text {
   margin-top: 91px;
   height: calc(~"100% - 101px");
   overflow-x: auto;
   overflow-y: auto;
   .win-title {
     padding: 35px 35px 0;
     font-size: 32px;
     color:#333;
     border-bottom: 1PX solid #F5F5F5;
      p {
       text-align: center;
     }
     .win-time {
       display: flex;
       justify-content: space-between;
       margin-bottom: 20px;
       font-size: 20px;
       margin-top: 20px;
       .win-c {
          color:#999;
       }
     }
       .win-one {
        font-size: 24px;
        color:#666;
        margin-bottom: 10px;
      }
   }
   .win-to {
     height: 88px;
     display: flex;
     align-items: center;
     justify-content: space-between;
     font-size: 32px;
     color:#333;
     padding: 0 35px;
     border-bottom: 1PX solid #f5f5f5;
   }
   .win-contant {
     padding: 35px;
   }
 }
}
</style>



