<!--  DOM  -->
<template>
    <div class="affairs">
        <!--  -->
        <!-- <v-ser :selecTxt="''"></v-ser> -->
        <div class="box">
            <!-- list -->
            <template v-if="isajax">
                <template v-if="list.length>0">
                    <!-- <van-pull-refresh v-model="loading" @refresh="onRefresh"> -->
                        <van-list finished-text="" @load="onLoad"  :error.sync="error" error-text="" :offset="200" :finished="finished" :immediate-check="false">
                            <v-con :type="'yj'" v-for="(el,i) in list" :key="i" :obj='el'></v-con>
                        </van-list>
                    <!-- </van-pull-refresh> -->
                </template>
                <template v-else>
                    <v-not :isError="isError"></v-not>
                </template>
            </template>
            <template v-else>
                <van-loading size="50px"></van-loading>
            </template>
        </div>
    </div>
</template>
<script>
import listCon from '@/components/enterprise/listCon'
import search from '@/components/enterprise/search'
import not from '@/components/not'
export default {
    name: 'affairs', // 
    data() {
        return {
            // 
            loading:false,
            // search:'',
            data:{
                comId:'', // ID
                type:'page',
                pageNo:1,
                pageSize:5
            },
            list:[],
            isScroll:true,
            isajax:false,//
            isError:false,//
            finished:false,//
            error:false,
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    components:{
        'v-con':listCon,
        'v-ser':search,
        'v-not':not
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
          this.data.comId = this.$route.query.id
            this.ajax();
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        // searchFn(){//

        // },
        onRefresh(){
            this.list=[];
            setTimeout(() => {
                this.data.pageNo=1;
                this.ajax();
            }, 500);
        },
        onLoad(){
            if(!this.isScroll){
                return false
            }
            this.data.pageNo++;
            this.ajax();
        },
        ajax(){
            this.isScroll=false;
            let that=this;
            this.$http({
                method:'post',
                url: '/project/company/list',
                data:that.data
            }).then(function(res){
                that.showLoad=false;
                if(that.list.length==0||that.data.pageNo==1){
                    that.list=res.data.data;
                    that.isajax=true;
                }else{
                    that.list=that.list.concat(res.data.data)
                }
                if(res.data.total==that.list.length||that.list.length<that.data.pageSize){
                    that.finished=true;//list
                }
                that.isScroll=true;
            }).catch(function(res){
                that.isajax=true;
                that.isError=true;
                if(that.list.length>0){
                    that.error = true;
                }
            })
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
.box{
    background: #f5f5f5;
    padding: 18px 32px;
    
}
</style>



