<!--  DOM  -->
<template>
    <div class="conform">
        <top-back :title='name'></top-back>
        <div class="form-top">
              <van-list
                 v-model="loading"
                 :finished="finished"
                 finished-text=""
                 @load="onLoad"
                  :offset="100"
                   :immediate-check="false"
              >
                <com-pany v-for="(el,i) in list" :key="i" :obj='el'></com-pany>
              </van-list>
           
        </div>
    </div>
</template>
<script>
import topBack from '@/components/topback'
import comPany from '@/components/company'
export default {
    name: 'conform', // 
    data() {
        return {
            // .
           name:'',
           list:[],
           loading:false, 
           finished:false,
           current:1,
           total:0,
           source:''
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    components: {
      'top-back':topBack,
      'com-pany':comPany   
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
         this.id = this.$route.query.id
         this.source = this.$route.query.source
         let that=this;
            this.$http({
                method:'post',
                url: '/notice/queryCompanyList/' + that.id,
                data:{
                    source:that.source,
                    pageNo:that.current,
                    pageSize:5
                }
            }).then(function(res){
                console.log(res);
                that.list = res.data.data
                that.total = res.data.total
            })
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        onLoad() {
              setTimeout(() => {
                  this.current = this.current + 1
                   let that=this;
                    this.$http({
                    method:'post',
                    url: '/notice/queryCompanyList/' + that.id,
                    data:{
                        source:that.source,
                        pageNo:that.current,
                        pageSize:5
                    }
                    }).then(function(res){
                        console.log(res.data)
                       res.data.data.forEach( el => {
                           that.list.push(el)
                       })
                    })
                  // 
                  that.loading = false;

                  // 
                  if (that.list.length >= that.total) {
                    that.finished = true;
                  }
                }, 500);

        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped  lang="less">
.conform {
position: absolute;
 top: 0;
 left: 0;
 right: 0;
 bottom: 0;   
  .form-top {
    margin-top: 91px;
    height: calc(~"100% - 101px");
    overflow-x: hidden;
    overflow-y: auto;
  }  
}

</style>



