<!--  DOM  -->
<template>
    <div class="affairs">
        <!--  -->
        <v-ser :iszb="true" @searchFn="searchFn"></v-ser>
        <div class="box">
            <!-- list -->
            <template v-if="isajax">
                <template v-if="bidList.length>0">
                    <!-- <van-pull-refresh v-model="loading" @refresh="onRefresh"> -->
                        <van-list finished-text="" :error.sync="error" error-text="" :offset="200" :finished="finished"  @load="onLoad" :immediate-check="false">
                            <v-con :type="'zb'" v-for="(el,i) in bidList" :key="i" :obj='el' ></v-con>
                        </van-list>
                    <!-- </van-pull-refresh> -->
                </template>
                <template v-else>
                    <v-not :isError="isError"></v-not>
                </template>
            </template>
            <template v-else>
                <van-loading size="50px"></van-loading>
            </template>
        </div>
    </div>
</template>
<script>
import listCon from '@/components/enterprise/listCon'
import search from '@/components/enterprise/search'
import not from '@/components/not'
export default {
    name: 'affairs', // 
    data() {
        return {
            // 
            showLoad:true,
            loading:false,
            bidList:[],
            data:{
                pageNo:1,
                pageSize:10,  //
                type:2,
                regions:'',  //
                com_name:'',  //
                title:'',
                sumType:'zhongbiao'
            },
            isScroll:true,
            isajax:false,//
            isError:false,//
            finished:false,//
            error:false,
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    components:{
        'v-con':listCon,
        'v-ser':search,
        'v-not':not
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        this.data.com_name = this.$route.query.name;
        this.data.regions=this.$route.query.source;
        this.ajax();
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        searchFn(option){//
            this.isajax=false;
            this.bidList=[];
            this.showLoad=true;
            this.data.title=option;
            this.ajax();
        },
        onLoad(){
            if(!this.isScroll){
                return false
            }
            this.data.pageNo++;
            this.ajax();
        },
        onRefresh(){
            this.bidList=[];
            setTimeout(() => {
                this.data.pageNo=1;
                this.ajax();
            }, 500);
        },
        ajax(){
            this.isScroll=false;
            let that=this;
            this.$http({
                method:'post',
                url: '/notice/queryList',
                data:that.data
            }).then(function(res){
                that.showLoad=false;
                if(that.bidList.length==0||that.data.pageNo==1){
                    that.bidList=res.data.data;
                    that.isajax=true;
                }else{
                    that.bidList=that.bidList.concat(res.data.data)
                }
                if(res.data.total==that.bidList.length||that.bidList.length<that.data.pageSize){
                    that.finished=true;//list
                }
                that.isScroll=true;
            }).catch(function(res){
                that.isajax=true;
                that.isError=true;
                if(that.bidList.length>0){
                    that.error = true;
                }
            })
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
.box{
    background: #f5f5f5;
    padding: 18px 32px;
    
}
</style>



