<!--  DOM  -->
<template>
    <div class="affairs">
        <!--  -->
        <!-- <v-ser @searchFn="searchFn"></v-ser> -->
        <div class="box">
            <!-- total-->
            <div class="title">{{companyLaw.lawBri+companyLaw.briCount}}{{companyLaw.lawJud+companyLaw.judCount}},{{companyLaw.lawTotal+companyLaw.total}}</div>
            <!-- list -->
            <template v-if="isajax">
                <template v-if="lawLsit.length>0">
                    <!-- <van-pull-refresh v-model="loading" @refresh="onRefresh"> -->
                        <van-list finished-text=""  :error.sync="error" error-text="" :offset="200" :finished="finished" @load="onLoad" :immediate-check="false">
                            <v-con v-for="(el,i) in lawLsit" :key="i" :obj='el'></v-con>
                        </van-list>
                    <!-- </van-pull-refresh> -->
                </template>
                <template v-else>
                    <v-not :isError="isError"></v-not>
                </template>
            </template>
            <template v-else>
                <van-loading size="50px"></van-loading>
            </template>
        </div>
        <van-popup v-model="mask"  position="bottom" :overlay="true">
            <van-datetime-picker
            type="year"
            :formatter="dateConfirm"
            v-model="date"
            @confirm="confirm"
            @cancel="mask=false"
            ></van-datetime-picker>
            <!-- :min-date="dateObj.minDate"
            :max-date="dateObj.maxDate" -->
        </van-popup>
    </div>
</template>
<script>
import listCon from '@/components/enterprise/listCon'
import search from '@/components/enterprise/search'
import not from '@/components/not'
export default {
    name: 'affairs', // 
    data() {
        return {
            // 
            loading:false,
            lawLsit:[],
            date:'',
            isSearch:true,
            ajaxData:{
                pageNo:1,
                pageSize:5,
                keyWord:'',
                start:null,
                end:null,  // 20162016
                comName:'' // 
            },
            mask:false,
            companyLaw:{
                briCount:0,
                judCount:0,
                lawBri: "",
                lawJud: "",
                lawTotal: "",
                total: 0
            },
            isScroll:true,
            isajax:false,//
            isError:false,//
            finished:false,//
            error:false,
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    components:{
        'v-con':listCon,
        'v-ser':search,
        'v-not':not
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        this.ajaxData.comName=this.$route.query.name
         this.ajax();
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        searchFn(option){//
            this.isajax=false;
            this.lawLsit=[];
            this.showLoad=true;
            this.isSearch=true;
            this.ajaxData.keyWord=option;
            this.ajax();
        },
        onLoad(){
            if(!this.isScroll){
                return false
            }
            this.ajaxData.pageNo++;
            this.ajax();
        },
        onRefresh(){
            this.lawLsit=[];
            setTimeout(() => {
                this.isSearch=true;
                this.ajaxData.pageNo=1;
                this.ajax();
            }, 500);
        },
        ajax(){
            this.isScroll=false;
            let that=this;
            this.$http({
                method:'post',
                url: '/law/list',
                data:that.ajaxData
            }).then(function(res){
                that.showLoad=false;
                if(that.lawLsit.length==0||that.ajaxData.pageNo==1){
                    that.lawLsit=res.data.data;
                    that.isajax=true;
                }else{
                    that.lawLsit=that.lawLsit.concat(res.data.data)
                }
                if(res.data.data.total==that.lawLsit.length||that.lawLsit.length<that.ajaxData.pageSize){
                    that.finished=true;//list
                }
                that.isScroll=true;
            }).catch(function(res){
                that.isajax=true;
                that.isError=true;
                if(that.lawLsit.length>0){
                    that.error = true;
                }
            })
            if(!that.isSearch){
                return false
            }
            this.$http({
                method:'post',
                url:'/law/companyLaw',
                data:{
                    comName:that.ajaxData.comName
                }
            }).then(function(res){
                that.companyLaw=res.data.data;
                that.isSearch=false;
            })
        },
        dateConfirm(type,value){
          if (type === 'year') {
            return `${value}`;
          }
          return value;
        },
        confirm(){
            
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
.box{
    background: #f5f5f5;
    padding: 0 32px 18px;
    .title{
        background: #f5f5f5;
        height: 80px;
        color: #bbb;
        font-size: 24px;
        line-height: 80px
    }
}

</style>



