<!--  DOM  -->
<template>
    <div class="affairs">
        <!--  -->
        <v-ser :selecTxt="''" @searchFn="searchFn"></v-ser>
        <div class="box">
            <!-- list -->
            <template v-if="isajax">
                <template v-if="peoList.length>0">
                    <!-- <van-pull-refresh v-model="loading" @refresh="onRefresh"> -->
                        <van-list finished-text="" @load="onLoad" :error.sync="error" error-text="" :offset="200" :finished="finished" :immediate-check="false">
                            <v-con :type="'ry'" v-for="(el,i) in peoList" :key="i" :obj='el' ></v-con>
                        </van-list>
                    <!-- </van-pull-refresh> -->
                </template>
                <template v-else>
                    <v-not :isError="isError"></v-not>
                </template>
            </template>
            <template v-else>
                <van-loading size="50px"></van-loading>
            </template>
        </div>
        <van-popup v-model="mask"  position="bottom" :overlay="true">
            <van-picker :columns="personCategory" value-key="category" :loading="categoryLoad" show-toolbar @confirm="confirmFn" @cancel="mask=false" ref="picker"/>
        </van-popup>
    </div>
</template>
<script>
import listCon from '@/components/enterprise/listCon'
import search from '@/components/enterprise/search'
import not from '@/components/not'
export default {
    name: 'affairs', // 
    data() {
        return {
            // 
            mask:false,
            loading:false,
            personCategory:[],//
            categoryLoad:true,
            data:{
                keyWord:'', //
                comId:'5d86f82e66452e2db067e42ca327c629', // ID
                category:'', //
                pageNo:1,
                pageSize: 10,
                province: '' //
            },
            peoList:[],
            isScroll:true,
            isajax:false,//
            isError:false,//
            finished:false,//
            error:false,
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    components:{
        'v-con':listCon,
        'v-ser':search,
        'v-not':not
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        this.data.comId = this.$route.query.id
        this.data.province = this.$route.query.source
        let that=this;
        this.ajax();
        this.$http({
            method:'post',
            url:'/company/personCategory/'+this.data.comId
        }).then(function(res){
            that.personCategory=res.data.data;
            that.personCategory.unshift({category:''});
            that.categoryLoad=false
        })
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        searchFn(option){//
            this.isajax=false;
            this.peoList=[];
            this.data.keyWord=option;
            this.ajax();
        },
        onRefresh(){
            this.peoList=[];
            setTimeout(() => {
                this.data.pageNo=1;
                this.ajax();
            }, 500);
        },
        onLoad(){
            if(!this.isScroll){
                return false
            }
            this.data.pageNo++;
            this.ajax();
        },
        ajax(){
            this.isScroll=false;
            let that=this;
            this.$http({
                method:'post',
                url: '/company/person',
                data:that.data
            }).then(function(res){
                if(that.peoList.length==0||that.data.pageNo==1){
                    that.peoList=res.data.data;
                    that.isajax=true;
                }else{
                    that.peoList=that.peoList.concat(res.data.data)
                }
                if(res.data.total==that.peoList.length||that.peoList.length<that.data.pageSize){
                    that.finished=true;//list
                }
                that.isScroll=true;
            }).catch(function(res){
                that.isajax=true;
                that.isError=true;
                if(that.peoList.length>0){
                    that.error = true;
                }
            })
        },
        confirmFn(){
            this.mask=false;
            this.peoList=[];
            this.data.category=this.$refs.picker.getValues()[0].category;
            if(this.data.category==''){
                this.data.category=''
            }
            this.ajax();
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
.box{
    background: #f5f5f5;
    padding: 18px 32px;
    
}
</style>



