<!--  DOM  -->
<template>
    <div class="award">
       <top-back :title='name'></top-back>

      <div class="award-list">
      <v-con :type="'gb'"  v-for="(el,i) in goodList" :key="i" :obj='el' ></v-con>
      </div>

      
    </div>
</template>
<script>
import topBack from '@/components/topback'
import listCon from '@/components/enterprise/listCon'
export default {
    name: 'award', // 
    data() {
        return {
            // 
            name:'',
            goodList:[],
            id:''
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    components: {
       'top-back':topBack,
       'v-con':listCon,
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        this.id = this.$route.query.id
        this.ajax()
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        ajax(){
            let that=this;
            this.$http({
                method:'post',
                url: '/company/reputation/' + that.id ,
                data:that.data
            }).then(function(res){
                if(res.data.data.allNum > 0 ) {
                  res.data.data.reputation.forEach(el => {
                    el.list.forEach(el => {
                      el.list.forEach(el => {
                         that.goodList.push(el)
                      })
                    })
                  })
                } else {
                  that.goodList.length = 0
                }  
            })
        },
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang='less'>
.award {
  background-color: #f5f5f5;
  .award-list {
    padding: 110px 35px 5px;
  }
}
</style>



