<!--  DOM  -->
<template>
    <!--  -->
    <div class="search-box">
        <template  v-if="!iszb">
            <div class="left" @click="leftTap">
                {{selecTxt}}
                <van-icon name="arrow-down"></van-icon>
            </div>
            <div class="right">
                <van-search placeholder="" v-model="search" @clear="clearFn" @search="searchFn"></van-search>
            </div>
        </template>
        <template v-else>
            <van-search placeholder="" v-model="search" @clear="clearFn" @search="searchFn"></van-search>
        </template>
    </div>
</template>
<script>
export default {
    name: 'affairs', // 
    data() {
        return {
            // 
            search:''
        }
    },
    watch: {
        // 
    },
    props: {
        // 
        selecTxt:{
            default:''
        },
        iszb:{
            default:false
        }
    },
    components:{

    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        searchFn(){//
            this.$emit('searchFn',this.search);
        },
        leftTap(){
            this.$parent.mask=true;
        },
        clearFn(){
            this.search=''
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
.search-box{
    height: 138px;
    background: #fff;
    display: flex;
    padding: 20px 30px;
    justify-content: space-between;
    box-sizing: border-box;
    align-items: center;
    .right{
        width: 72%;
        height: 100%;
    }
    .van-search{
            background: #f5f5f5 !important;
            border-radius: 5px
        }
    .van-search__content{
        background: #f5f5f5;
    }
}
</style>



