<!--  DOM  -->
<template>
    <div class="letter">
      <top-back :title='name'></top-back>
      <div class="letter-de">
         <div class="letter-title">
            <div class="letter-name">
              <p>
               {{detail.comName}}
              </p>
              <div class="letter-save">
                  {{detail.subsist}}
              </div>
            </div>
            <div class="letter-lead">
              <span  style="color:#FE6603">
                {{detail.legalPerson}}
              </span>
              <span class="person">
                 
              </span>
            </div>
            <div class="letter-iphone">
              <van-icon name="phone-o" />
              <span class="iphone">
                 {{detail.phone}}
              </span>
              <span class="le-col" @click="more">
                
              </span>
            </div>
            <div class="letter-url">
               <van-icon name="location-o" />
               <p style="color:#666666">
                  {{detail.comAddress}}
               </p>               
            </div>
         </div>

        <div class="letter-nav">
          <div>
              <span v-for="(o,i) of navList" :key="i" :class="navNum==i?'active':''" @click="navNum=i">{{o}}</span>
          </div>
        </div>
        <div class="letter-detail">
            <template v-if="navNum==0">
              <!--  -->
              <v-gs></v-gs>
            </template>
            <template v-else-if="navNum==1">
              <!--  -->
              <v-fw></v-fw>
            </template>
            <template v-else-if="navNum==2">
              <!--  -->
              <v-zz></v-zz>
            </template>
            <template v-else-if="navNum==3">
              <!--  -->
              <v-ry></v-ry>
            </template>
            <template v-else-if="navNum==4">
              <!--  -->
              <v-yj></v-yj>
            </template>
            <template v-else-if="navNum==5">
              <!--  -->
              <v-zb></v-zb>
            </template>
             <template v-else-if="navNum==6">
              <!--  -->
              <v-gl></v-gl>
            </template>
        </div>

      </div>
      <div class="popup-box" v-if="mask">
        <div class="box-con">
            <div class="top-box">
              <span>APP</span>
            </div>
            <div class="bom-box">
              <span class="canel" @click="canelFn"></span>
              <span class="sure" @click="sureFn">APP</span>
            </div>
        </div>
      </div>
      
      <v-load v-if="isload"></v-load>
    </div>
</template>
<script>
import topBack from '@/components/topback'
import commerce from '@/components/enterprise/commerce'
import affairs from '@/components/enterprise/affairs'
import qual from '@/components/enterprise/qual'
import people from '@/components/enterprise/people'
import achievement from '@/components/enterprise/achievement'
import bid from '@/components/enterprise/bid'
import good from '@/components/enterprise/good'
import loading from '@/components/loading'
export default {
    name: 'letter', // 
    data() {
        return {
            // 
             name:'',
             detail:{},
             navList:['','','','','','',''],
             navNum:0,
             isload:true,
             mask:false,
            //  path:'/commerc'
        }
    },
    watch: {
        // 
    },
    components: {
       'top-back':topBack,
       'v-fw':  affairs,
       'v-gs':commerce,
       'v-zz':qual,
       'v-ry':people,
       'v-yj':achievement,
       'v-zb':bid,
       'v-gl':good,
       'v-load':loading
    },
    props: {
        // 
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        this.id = this.$route.query.id
         let that=this;
            this.$http({
                method:'post',
                url: '/company/' + this.id,
                data:{
                   
                }
            }).then(function(res){
                that.detail = res.data.data;
                var arr = []
                arr = that.detail.phone.split(';')
                that.detail.phone = arr[0]
                that.isload=false
             })
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        more(){
          this.mask=true;
          this.modalHelper.afterOpen();
        },
        canelFn(){
          this.mask=false;
          this.modalHelper.beforeClose();
        },
        sureFn(){
          this.mask=false;
          this.modalHelper.beforeClose();
           window.location.href='https://a.app.qq.com/o/simple.jsp?pkgname=com.yaobang.biaodada';
        }

    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang='less'>
.letter {
//  position: absolute;
//  top: 0;
//  left: 0;
//  right: 0;
//  bottom: 0;
 
 .le-col{
   color:#FE6603;
 }
 .letter-de {
   margin-top: 100px;
   height: calc(100% - 101px);
   background-color: #f5f5f5;
   .letter-title {
     padding: 49px 35px 31px;
     background-color: #fff;
     margin-bottom: 16px;
    .letter-name {
      font-size: 32px;
      color:#333;
      display: flex;
      align-items: center;
      margin-bottom: 47px;
      p {
        max-width: 550px;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
      }
    }
     .letter-save {
       height: 39px;
       width: 70px;
       border: 1PX solid #FE6603;
       color:#FE6603;
       border-radius: 8px;
       text-align: center;
       line-height: 39px;
       box-sizing: border-box;
       margin-left: 29px;
       font-size:24px;
     }
   }
   .letter-lead {
     font-size: 28px;
     color:#FE6603;
     margin-bottom: 40px;
     .person {
       font-size: 24px;
       color:#999;
       margin-left: 15px;
     }
   }
   .letter-iphone {
     font-size: 28px;
     display: flex;
     align-items: center;
     margin-bottom: 40px;
     i {
       font-size: 36px;
       color:#FE6603;
     }
     .iphone {
       margin-left: 16px;
       margin-right: 36px;
       color:#FE6603;
     }
   }
   .letter-url {
     font-size: 28px;
     display: flex;
     align-items: center;
     box-sizing: border-box;
     color:#666;
     i {
       font-size: 36px;
       margin-right: 15px;
     }
     p {
       width: 100%;
       overflow: hidden;
       text-overflow:ellipsis;
       white-space: nowrap;
     }
   }
 }
 .letter-nav {
   height: 96px;
   background-color: #fff;
  //  padding: 0 32px;
   overflow-x:scroll;
   line-height: 96px;
   border-bottom: 1PX solid #F2F2F2;
    div{
      width: 126%;
      span{
        width: 14%;
        display: inline-block;
        text-align: center;
      }
      .active{
        color: #FE6603
      }
    }
   
 }
}
.popup-box{
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100vh;
  background: rgba(0,0,0,.5);
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 0;
  .box-con{
    width: 548px;
    height: 291px;
    border-radius: 10px;
    background: #fff;
  }
  .top-box{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 196px;
    span{
      width: 360px;
      display: inline-block;
      color: #999;
    }
  }
  .bom-box{
    border-top: 1PX solid #F2F2F2;
    height: 90px;
    line-height: 90px;
    display: flex;
    span{
      display: inline-block;
      width: calc((100% - 1PX)/2);
      text-align: center;
    }
    .canel{
      border-right: 1PX solid #F2F2F2;
      color: #333;
    }
    .sure{
      color: #FE6603
    }
  }
}
</style>



