<template>
<div class="bid">
  <v-fix :nav="1"></v-fix>
  <!--  -->
  <div class="search">
    <van-search placeholder="" v-model="data.title" @search="searchFn" @clear="clearFn"></van-search>
  </div>
  <!--  -->
  <div class="screen-box">
    <div class="condition" :class="{'active':o.active}" v-for="(o,i) of screenList" :key="i" @click="showMask(i)">{{o.txt}}</div>
    <v-addr @addObj="returnAddress" v-if="screenList[0].active" :add="data.regions"></v-addr>
    <v-type @sureFn='typeSure' @canleFn="typeCanle" v-if="screenList[1].active" :num="screenNum.typeNum"></v-type>
    <v-assess :selectArr="screenNum.select" @sureFn='assessSure' @canleFn="typeCanle" v-if="screenList[2].active"></v-assess>
    <v-apt v-if="screenList[3].active" @sureFn='aptSure' @recordFn="recordFn" :type="0" :arr="screenNum.arr"></v-apt>
  </div>
  <!--  -->
  <div class="total">{{total}}</div>
  <!--  -->
  <template v-if="isajax">
    <template v-if="zbList.length>0">
      <!-- <van-pull-refresh v-model="loading" @refresh="onRefresh"> -->
        <van-list finished-text=""  @load="onLoad" :error.sync="error" error-text="" :offset="200" :finished="finished" :immediate-check="false">
          <v-zb v-for="(o,i) of zbList" :key="i" :obj="o"></v-zb>
        </van-list>
      <!-- </van-pull-refresh>   -->
    </template>
    <template v-else>
        <v-not :isError="isError"></v-not>
    </template>
  </template>
  <template v-else>
    <van-loading size="50px"></van-loading>
  </template>
</div>
</template>
<script>
import zbCon from '@/components/zbContent'
import fixHead from '@/components/fixHead'
import search from '@/components/search'
import addr from '@/components/address'
import getType from '@/components/getType'
import assess from '@/components/assess'
import aptitude from '@/components/aptitude'
import not from '@/components/not'
export default {
    data () {
      return {
        isScroll:true,
        zbList:[],
        loading:false,//false
        data:{
          pageNo:1,
          pageSize:'10',
          regions:'',
          type: "0",
          projectType:'',
          title: "",
          pbModes:'',
          zzType:''
        },
        total:0,
        screenList:[
          {
            txt:'',
            active:false,
          },{
            txt:'',
            active:false
          },{
            txt:'',
            active:false
          },{
            txt:'',
            active:false
          },
        ],
        screenNum:{
          typeNum:0,
          select:[],
          arr:[],
        },
        isajax:false,//
        isError:false,//
        finished:false,//
        error:false,
      }
    },
    methods: {
      onRefresh(){//
        this.zbList=[];
        setTimeout(() => {
          this.data.pageNo=1;
          this.ajax();
        }, 500);
      },
      ajax(){
        //
        this.isScroll=false;
        let that=this;
        this.$http({
            method:'post',
            url: '/notice/queryList',
            data:that.data
        }).then(function(res){
            that.loading = false;
            that.total=res.data.total;
            if(that.zbList==0||that.data.pageNo==1){
              that.zbList=res.data.data;
              that.isajax=true;
            }else{
              that.zbList=that.zbList.concat(res.data.data)
            }
            if(res.data.total==that.zbList.length||that.zbList.length<that.data.pageSize){
                that.finished=true;//list
            }
            that.isScroll=true;
        }).catch(function(res){
            that.isajax=true;
            that.isError=true;
            if(that.zbList.length>0){
                that.error = true;
            }
        })
      },
      onLoad(){//
        if(!this.isScroll){
            return false
        }
        this.data.pageNo++;
        this.ajax();
      },
      searchFn(){//
        this.isajax=false;
        this.zbList=[];
        this.data.pageNo=1;
        this.ajax();
      },
      returnAddress(option){//
        this.isajax=false;
        this.zbList=[];
        this.screenList[0].active=false;
        this.screenList[0].txt=option;
        this.data.regions=option;
        this.data.pageNo=1;
        this.ajax();
      },
      typeSure(option){//
        this.isajax=false;
        this.zbList=[];
        this.screenList[1].active=false;
        this.data.projectType=option.str;
        this.screenNum.typeNum=option.index;
        this.data.pageNo=1;
        this.ajax();
      },
      assessSure(option){
        this.isajax=false;
        this.zbList=[];
        this.screenList[2].active=false;
        this.data.pbModes=option.str;
        this.screenNum.select=option.select;
        this.data.pageNo=1;
        this.ajax();
      },
      showMask(i){//
        if(this.screenList[i].active){
          this.screenList[i].active=false
        }else{
          for(let x of this.screenList){
            x.active=false
          }
          this.screenList[i].active=true;
        }
      },
      typeCanle(){
        this.screenList[1].active=false;
        this.screenList[2].active=false;
        this.screenList[3].active=false;
      },
      aptSure(option){
        this.isajax=false;
        this.zbList=[];
        // console.log(option);
        this.screenList[3].active=false;
        this.data.zzType=option.str;
        this.screenNum.arr=option.list;
        this.data.pageNo=1;
        this.ajax();
      },
      clearFn(){
        this.data.title=''
      },
      recordFn(){
        this.screenList[3].active=false;
        this.data.zzType='';
        this.isajax=false;
        this.zbList=[];
        this.screenNum.arr=[];
        this.data.pageNo=1;
        this.ajax();
      },
    },
    components:{
        'v-zb':zbCon,
        'v-fix':fixHead,
        'v-search':search,
        'v-addr':addr,
        'v-type':getType,
        'v-assess':assess,
        'v-apt':aptitude,
        'v-not':not
    },
    created(){
      this.data.title = this.$route.query.search ?  this.$route.query.search : '';
      this.data.regions = sessionStorage.getItem('address');
      this.screenList[0].txt=sessionStorage.getItem('address');
      this.ajax();
    }
}
</script>
<style lang="less" scoped>
.bid {
  padding-top: 200px;
  /**/
  .screen-box{
    position: relative;
    height: 80px;
    display: flex;
    background: #fff;
    align-items: center;
    font-size: 28px;
    padding: 0 32px;
    justify-content: space-between;
    .condition{
      display: flex;
      align-items: center;
    }
    .condition.active{
      color: #FE6603
    }
    .condition.active::after{
      content:'';
      width: 0;
      height: 0;
      margin-left: 12px;
      margin-bottom: 7px;
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-bottom: 7px solid #FE6603;
      border-top: 7px solid transparent;
    }
    .condition::after{
      content:'';
      width: 0;
      height: 0;
      margin-left: 12px;
      margin-top: 7px;
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-top: 7px solid #333;
      border-bottom: 7px solid transparent;
    }
  }
  /**/
  .total{
    font-size: 24px;
    color: #999;
    padding: 24px 32px;
    background: #f5f5f5
  }
}
</style>


