<!--  DOM  -->
<template>
    <div class="company">
       <div class="c-title">
         <img src="../assets/company.png" alt="">
         <p>
           {{obj.comName}}
         </p>
       </div>
       <p class="c-pl">
          {{obj.legalPerson}}
       </p>
       <p class="c-pl">
          {{obj.phone}}
       </p>
       <p class="c-pl c-b">
          {{obj.comAddress}}
       </p>
    </div>
</template>
<script>
export default {
    name: 'company', // 
    data() {
        return {
            // 
        }
    },
    watch: {
        // 
    },
    props: {
        // 
        obj:{}
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped  lang="less">
.company {
  border-bottom: 1PX solid #F0F0F0;
  .c-title {
    margin: 41px 31px 30px;
    display: flex;
    align-items: center;
    color:#333;
    font-weight: 550;
    img {
      margin-right: 35px;
    }
  }
  .c-pl {
    padding-left: 102px;
    color:#999;
    font-size: 28px;
    margin-bottom: 23px;
    width: 100%;
    padding-right: 31px;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
    box-sizing: border-box;
  }
  .c-b {
    margin-bottom: 40px;
  }
}

</style>



