import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import Bid from '@/pages/bid'
import Detail from '@/pages/detail'
import Conform from '@/pages/conform'
import testZs from '@/pages/testZs'
import Winning from '@/pages/winning'
import zhongBid from '@/pages/zhongBid'
import Letter from '@/pages/letter'
import companyList from '@/pages/companyList'
import commerce from '@/components/enterprise/commerce'
import affairs from '@/components/enterprise/affairs'
import qual from '@/components/enterprise/qual'
import people from '@/components/enterprise/people'
import achievement from '@/components/enterprise/achievement'
import letBid from '@/components/enterprise/bid'
import good from '@/components/enterprise/good'
// import not from '@/components/not'
import award from '@/pages/award'
import badness from '@/pages/badness'





Vue.use(Router)

export default new Router({
  routes: [
    {//
      path: '/',
      name: 'index',
      component: index
    }, 
    { //
      path: '/zhongBid',
      component: zhongBid,
      name:'zhongBid'
    },{ //
      path: '/bid',
      component: Bid,
      name:'bid' 
    },{ //
      path: '/companyList',
      component: companyList,
      name:'companyList' 
    },
    {
      path: '/detail',
      component: Detail
    },
    { 
      path: '/conform',
      component: Conform 
    },
	  {
      path: '/testZs', 
      name: 'testZs',
      component: testZs
    },
    { 
      path: '/winning', 
      component: Winning 
    },
    { //
      path: '/letter', 
      component: Letter 
    },
    { //
      path: '/commerce', 
      component: commerce 
    },
    {//
      path:'/affairs',
      component:affairs,
      name:'affairs'
    },
    {  // -
      path: '/qual',
      component: qual,
      name: 'qual'
    },
    {   // 
      path: '/people',
      component: people,
      name: 'people'
    },
    {   // 
      path: '/achievement',
      component: achievement,
      name: 'achievement'
    },
    {   // 
      path: '/letbid',
      component: letBid,
      name: 'letbid'
    },
    {   // 
      path: '/good',
      component: good,
      name: 'good'
    },
    {   // 
      path: '/award',
      component: award,
      name: 'award'
    },
    {   // 
      path: '/badness',
      component: badness,
      name: 'badness'
    }
  ]
})



