}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
 .top-nav {
   height: 90px;
   width: 100%;
   background-color: #FE6603;
   line-height: 90px;
   text-align: center;
   color:#fff;
   font-size: 36px;
   position: fixed;
   z-index: 99;
   top: 0;
   left: 0;
   .top-left {
       font-size: 50px;
       color:#fff;
       position: absolute;
       left: 20px;
       top: 50%;
       transform: translateY(-50%);
   }
 }
</style>



