<!--  DOM  -->
<template>
    <div class="nott">
      
      <div v-if="!isError">
        <img src="../assets/nolist.png" alt="">
        <div class="not-f" >
          
        </div>
      </div>
        <div class="error" v-else>
            <img src="../assets/load-error.png" alt="">
            <div class="not-f" ></div>
            <div class="not-f" @click="clickTap" style="color:#FE6603"></div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'not', // 
    data() {
        return {
            // 
        }
    },
    inject:['reload'],
    watch: {
        // 
    },
    props: {
        // 
        isError:{
            default:false
        }
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        clickTap(){
            this.reload();
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang='less'>
.nott {
 display: flex;
 justify-content: center;
 img {
   margin-top: 138px;
   width: 250px;
 }
 .not-f {
   margin-top: 37px;
   font-size: 28px;
   color:#666;
   text-align: center;
 }
 .error{
     img{
         width: 239px;
         margin-top: 150px;
     }
 }
}
</style>



