<!--  -->
<template>
    <div class="testZs">
        <input type="date"/>
        <ul>
            <li v-for="(x,o) of list" :key="o">
                <div class="top box">
                    <div class="title">{{x.title}}</div>
                    <div class="time">{{x.opendate}}</div>
                </div>
                <div class="bottom box">
                    <template>
                        <div class="name" v-if="x.oneName">{{x.oneName}}</div>
                        <div class="name" v-else></div>
                    </template>
                    <template>
                        <div class="name" v-if="x.oneOffer">{{x.oneOffer}}</div>
                        <div class="name" v-else></div>
                    </template>
                </div>
            </li>
        </ul>
        <van-pagination v-model="currentPage" @change="ajax" :total-items="total"  :items-per-page="10" :force-ellipses="true"/>
    </div>
</template>
<script>
export default {
    name: 'testZs', // 
    data() {
        return {
            // 
            list:[],
            currentPage:1,
            total:1,
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        let that=this;
        //
        this.$http({
            method:'post',
            url: '/notice/queryList',
            data:{
                pageNo:that.currentPage,
                pageSize:10,
                regions:'',
                type: "2"
            }
        }).then(function(res){
            that.list=res.data.data;
            that.total=res.data.total
        })
        // console.group('===============created');
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        ajax(){
            let that=this;
            //
            this.$http({
                method:'post',
                url: '/notice/queryList',
                data:{
                    pageNo:that.currentPage,
                    pageSize:10,
                    regions:'',
                    type: "2"
                }
            }).then(function(res){
                that.list=res.data.data;

            })
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less" >
.testZs .van-pagination{
    font-size: 16PX;
    width: 70%;
    margin: 0 auto;
    height: 60PX;
    li.van-pagination__item--active{
        background: #ec7522
    }
    li.van-pagination__item{
        color: #ec7522
    }
}
.testZs{
    width: 1020PX;
    margin: 0 auto
}
input{
    font-size: 16PX
}
ul{
    margin-bottom: 40PX
}
li{
    list-style: decimal;
    border-bottom: 1PX solid #f2f2f2;
    font-size: 16PX;
    .box{
        display: flex;
        justify-content: space-between;
        margin-bottom: 5PX;
    }
    .title{
        width: 750PX;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }
    .name{
        color: #ec7522;
    }
}
</style>



